import React from 'react'
import 'isomorphic-fetch'

const Home = () => {
    return(
        <h1>Hello Next</h1>
    )
}

Home.getInitialProps = async () => {
    const response = await fetch('https://api.github.com/orgs/rocketseat/repos')
        , repositories = await response.json()

    return repositories
}

export default Home